import * as React from "react"
import { Link } from "gatsby"
import MainIcon from '../images/logo-light.svg'
import 'font-awesome/css/font-awesome.min.css';


const Header = () => (
  <>
    <header className="header_section">
      <div className="container">
        <nav className="navbar navbar-expand-lg custom_nav-container ">
          <Link to="/" className="navbar-brand">
            <img src={MainIcon} alt="" />
          </Link>
          <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
            <span className=""> </span>
          </button>
          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav">
              
              
              
              <li className="nav-item" >
                <Link className="nav-link" to="/">Компания</Link>
              </li>
              
              
              
              <li className="nav-item">
                <div className="dropdown5">
                  <li className="dropbtn5" to="">Направления
                    <i className="fa fa-caret-down"></i>
                  </li>
                  <div className="dropdown-content5">
                    <a href="#">Производство и Торговля</a>
                    <a href="#">Строительство и Проектирование</a>
                    <a href="#">Сельское Хозяйство</a>
                    <a href="#">Аренда VIP Авто</a>
                    <a href="#">Кинематография</a>
                  </div>
                </div>
              </li>
              
              
              
              
              <li className="nav-item" >
                <Link className="nav-link" to="/miras">Магазин</Link>
              </li>
              <li className="nav-item" >
                <Link className="nav-link" to="/miras">Карьера</Link>
              </li>
              <li className="nav-item" >
                <Link className="nav-link" to="/miras">Контакты</Link>
              </li>
            </ul>
          </div>
        </nav>
      </div>
    </header>
  </>
)

export default Header
